.order-container {
  position: absolute;
  left: 0;
  width: 100%;
  height: calc(100%);
  background-color: rgba($color: #000000, $alpha: 0.4);

  .order-div {
    position: absolute;
    left: 50%;
    top: 120px;
    transform: translateX(-50%);
    width: 540px;
    height: auto;
    background-color: #fff;
    padding: 48px;
  }

  h3 {
    margin: 33px 0 24px;
  }

  .recap-div {
    margin: 33px 0 46px;
    width: 100%;
    height: auto;
  }

  .left-div {
    border-radius: 8px 0 0 8px;
    padding: 24px;
    width: 270px;
    height: auto;
    background-color: $color-grey;
  }

  .recapItem-div {
    height: 64px;
    overflow-y: hidden;
  }

  .changeHeight {
    height: auto;
  }

  .othersItem-div {
    margin-top: 12px;
    text-align: center;
    width: 100%;
    height: 30px;
    border-top: 1px solid rgba($color: #000000, $alpha: 0.08);
    cursor: pointer;

    p {
      margin-top: 12px;
    }
  }

  .right-div {
    width: 174px;
    height: auto;
    background-color: #000000;
    border-radius: 0 8px 8px 0;
    padding: 20px 0 20px 32px;
  }

  .total-div {
    align-self: center;
    color: #fff;
    width: max-content;
    height: 57px;

    p {
      opacity: 0.6;
      margin-bottom: 8px;
    }
  }

  .changePosition {
    align-self: flex-end;
  }
}

@media screen and (max-width: 590px) {
  .order-container {
    .order-div {
      width: 90%;
    }

    .left-div {
      padding: 15px;
    }
  }
}

@media screen and (max-width: 560px) {
  .order-container {
    .order-div {
      .recap-div {
        flex-direction: column;
      }

      .left-div,
      .right-div {
        width: 100%;
      }

      .left-div {
        border-radius: 8px 8px 0 0;
      }

      .right-div {
        border-radius: 0 0 8px 8px;
      }
    }
  }
}

@media screen and (max-width: 410px) {
  .order-container {
    .order-div {
      padding: 32px;
    }

    .left-div {
      padding: 24px 10px;
    }
  }
}

@media screen and (max-width: 340px) {
  .order-container {
    .order-div {
      padding: 25px;
    }
  }
}
